// Invested panel
.panel-invested {
    color: $black-0;

    .panel-body {
        padding: 44px 0 20px;

        .invested-item {
            position: relative;
            float: left;
            width: 50%;
            margin-bottom: 44px;
            padding-top: 25px;
            text-align: center;

            .invested-icon {
                position: absolute;
                top: 0;
                left: 50%;
                margin-left: -10px;
                width: 18px;
                height: 20px;
                background-repeat: no-repeat;
                background-position: center bottom;
                background-size: contain;
            }

            &.one {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-1.svg');
                }
            }

            &.two {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-2.svg');
                }
            }

            &.three {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-3.svg');
                }
            }

            &.four {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-4.png');
                }
            }

            &.five {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-5.svg');
                }
            }

            &.six {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-6.png');
                }
            }

            &.seven {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-7.png');
                }
            }

            &.eight {
                .invested-icon {
                    background-image: url('../img/icons-invested/ic-8.png');
                }
            }
        }

        .amount {
            font-size: 11px;
            line-height: 22px;
        }

        .currency {
            font-size: 11px;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
    }
}


// Bonus panel
.panel-bonus {
    .panel-body {
        padding-bottom: 50px;

        .bonus-item {
            float: left;
            width: calc(100% / 3);
            margin-top: 37px;
            text-align: center;
        }

        .percent {
            font-size: 16px;
            font-weight: bold;
        }

        .empty-scale {
            width: 147px;
            height: 5px;
            background-color: rgb(188, 209, 221);
            margin: 13px auto 23px;

            .full-scale {
                height: 5px;
                background-color: rgb(21, 159, 132);
                width: 100%;
            }
        }

        .time {
            font-size: 13px; text-align: center;
        }
    }
}


// Course panel
.panel-course {
    .panel-body {
        padding-bottom: 50px;

        .course-tip {
            padding: 20px 0;
            font-size: 17px;
            color: $gray_b6;
        }

        .panel-course-wrap {
            @include cf();
        }

        .course-list {
            float: left;
            width: 50%;
            font-size: 17px;
            color: $black-0;

            &:first-of-type {
                border-right: 1px solid $gray_e3;
            }

            &:last-of-type {
                padding-left: 35px;
            }
        }

        .course-item {
            padding: 13px 0;

            & > * {
                display: inline-block;
                vertical-align: middle;
            }

            .icon {
                width: 20px;
                height: 20px;
                background-color: $gray_c1;
                border-radius: 50%;
            }

            .currency {
                width: 100px;
                max-width: 115px;
                padding-left: 23px;
            }

            .amount {
                font-size: 15px;
            }
        }
    }
}
