.panel {
    margin-bottom: 20px;
    background-color: $white;
    border: solid 1px #e3e4e8;
    border-radius: 4px;

    .panel-title {
        padding: 15px 30px;
        font-size: 17px;
        font-weight: 400;
        color: $black-0;
        border-bottom: solid 1px #e3e4e8;
    }

    .panel-body {
        padding: 0 30px;
        @include cf();
    }
}

.panel-mini {
    height: 58px;
    margin-bottom: 20px;
    padding: 10px;
    font-size: 13px;
    color: $black;
    border-radius: 4px;
    background-color: $gray_a9;

    // Total balance
    &.total-balance {
        .title {
            margin-bottom: 5px;
        }

        .num-balance {
            font-size: 18px;
            font-weight: bold;
            color: $black;
        }
    }

    // Statistics
    &.statistics {
        .line {
            line-height: 19px;
            color: rgba(14, 24, 36, 0.5);

            span {
                margin-left: 5px;
                font-weight: bold;
                color: $black-0;
            }
        }
    }
}

.panel-status {
    .panel-title {
        padding-left: 15px;
        padding-right: 15px;
    }

    .status-body {
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 15px;

        .title {
            padding: 20px 0;
            font-size: 12px;
            line-height: 1.42;
            letter-spacing: 0.4px;
            color: $gray_94;
        }

        .currency,
        .count {
            display: block;
            padding-left: 25px;
            font-size: 17px;
            font-weight: bold;
            background-repeat: no-repeat;
            background-position: left center;
        }

        .invested-all {
            margin-bottom: 10px;

            .currency:first-of-type {
                margin-bottom: 15px;
            }

            .bnr {
                background-image: url('../img/icons/btc.svg');
            }

            .usd {
                background-image: url('../img/icons/usd.png');
            }
        }

        .members {
            .count {
                background-image: url('../img/icons/user.svg');
            }
        }
    }
}
