.container {
    width: 1170px;
    margin: 0 auto;
    @include cf();
}

.main-content {
    padding-top: 20px;
    @include cf();
}

.aside-left {
    float: left;
    width: 195px;
    margin-right: 10px;
}

.aside-right {
    float: left;
    width: 230px;
    margin-left: 10px;
}

.content {
    float: left;
    width: 550px;
    margin: 0 10px;

    &.content-large {
        width: 955px;
        margin-right: 0;
    }
}
